import React from "react"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { Box } from "@rp/react/web/mui/core"
import { ClosePaneButton } from "../../ClosePaneButton"
import { ChatPaneNewChatButton } from "../ChatPaneNewChatButton"

type ChatPaneHeaderProps = {
  close: () => void
  name: string | undefined
  handleNewChat: () => void
}

function ChatPaneHeader({ close, name, handleNewChat }: ChatPaneHeaderProps) {
  const text = `Chat with ${name ?? "Files"}`
  return (
    <Box
      sx={{
        borderBottom: theme => `1px solid ${theme.palette.grey.A700}`,
        p: 2,
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
      }}
    >
      <EllipsisTooltip
        tooltipText={text}
        typographyProps={{ fontSize: 20, fontWeight: 700, flex: 1 }}
      >
        {text}
      </EllipsisTooltip>

      <ChatPaneNewChatButton handleNewChat={handleNewChat} />

      <Box>
        <ClosePaneButton close={close} />
      </Box>
    </Box>
  )
}

export { ChatPaneHeader }
